* {
  box-sizing: border-box;
}
*::after, *::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

#app {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  color: #fff;
  background-color: #000;
  font-family: "Thintel";
}
#app .app-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#app .app-wrapper .arena {
  position: relative;
  width: 980px;
  height: 725px;
  background-image: url(../../assets/img/brief/arena.svg);
}

.education .education-wrap {
  padding-top: 130px;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.education .education-wrap .education-title {
  margin-bottom: 20px;
  bottom: calc(50% - 60px);
}
.education .education-mousemove {
  position: absolute;
  left: 0;
  right: 0;
  bottom: calc(50% - 60px);
  text-align: center;
}

.ship {
  position: absolute;
  opacity: 0;
}